        33: ["ייעודך להיות משפחתי, ביתי, אומנותי, יציב, עקבי, לשרת את הקהילה והמשפחה.","תעסוקה: טיפול, אומנות, יופי ואסטטיקה, מקצועות נותני שירות, מקצועות טכניים."],
        11: ["ייעודך להוביל דרך עולמית, להמציא משהו לאנושות, הבעיה היא שהרמה האנרגטית של 11 גבוהה מדי אז ברוב המקרים הם יהיו מספר 2.","תעסוקה: הוראה, במה, מסחר, אומנות, יופי ואסטטיקה, מוסיקה, ספורט."],
        22: ["הייעוד שלך להשאיר חותם ביקום, להיות מאסטר בתחום העסקי והרוחני. לפרנס מאות משפחות, להוביל דרך כלכלית, לפתח עסקים גדולים בארץ ובעולם, להיות מיוחד, לעמוד על במה ולהעביר ידע. בנוסף אתה צריך להמציא משהו למען האנושות. אם לא הגעת לדרגת מאסטר תעבוד במקצועות של 4.","תעסוקה: נטייה לעסקים, כספים, במה, ספורט, רפואה."],
        13: ["היעוד שלך קודם לעבור את התיקון הקרמתי, כל עוד לא תעבור את התיקון אין לך לאן להתקדם. חלק מהתיקון שלך זה לשמור על הבריאות.","תעסוקה: טיפול או שירות, מקצועות עם כפות הידיים."],
        14: ["ייעודך קודם לעשות את התיקון שהוא לא לפרק מערכות יחסים. להשתחרר מדחף בלתי נשלט לחופש מוגזם, לפתח יציבות משפחתית וכלכלית ולאזן את עצמך.","תעסוקה: נטייה למקצועות שירות, מסחר ועבודות בכפות ידיים."],
        16: ["ייעודך קודם לעשות את התיקון והוא להתחבר לנפש ולהתגבר על החרדות והלחצים, להשתחרר מתלות כלכלית, להימנע מהתנתקויות מקרוביך ואחר כך לעמוד על במה ולהעביר ידע","תעסוקה: נטייה למקצועות מדויקים, מקצועות טיפול או שירות, מקצועות עם כפות הידיים, הוראה, רפואה, יעוץ, ספורט, סטנדאפיסטים, במה."],
        19: ["ייעודך הוא קודם לעשות את התיקון והוא ללמוד ולהבין את הקושי שחווית בילדות, להשתחרר ממועקת הקורבנות ולהמשיך בחיים, ללמוד להיות המנהל ולא הסגן הנצחי, לאזן את עצמך ולהתפתח.","תעסוקה: ניהול, פיקוד, מסחר, כספים, במה, מקצועות של צווארון לבן, עבודה עם כסף."],
