        5: ["דומה ל- 3 בעל יכולת חשיבה טובה מאד, בלימודים הוא מצוין, בעסקים הוא טוב מאד ובזוגיות יש קושי בהתמדה. עוסק בספורט, מוזיקה, במה, מחשבים, אינטרנט, ראיית חשבון."],
