        7: ["יכולות חשיבה טובות מאד אבל מאד בעיתיים בזוגיות, מייצר חרדות ולחצים נפשיים. טוב מאד לשכיר רע מאד לעצמאי"],
        8: ["יכולות החשיבה הטובים בעולם כמו 1 רק בהבדל אחד: 8 קודם חושב אחר כך עושה, 1 קודם עושה אחר כך חושב. 8 מצוין לעסקים כעצמאי, מצוין כשכיר, מצוין לזוגיות, מצוין בכל התחומים."],
