import {FieldAttributes, useField, useFormikContext} from "formik";
import {MyErrorLabel, useID} from "./tools";
import {Form} from "semantic-ui-react";
import React from "react";

export const MyInput = ({label, id, ...props}: FieldAttributes<any>) => {
  // useField() returns [formik.getFieldProps(), formik.getFieldMeta()]
  // which we can spread on <input> and also replace ErrorMessage entirely.
  const [field, meta] = useField(props);
  const {isSubmitting} = useFormikContext();
  const myID = useID(id);

  return (
    <Form.Field error={meta.touched && !!meta.error}>
      <input disabled={isSubmitting} id={myID} placeholder={label} {...field} {...props}/>
      <MyErrorLabel error={meta.touched && meta.error ? meta.error : undefined}/>
    </Form.Field>
  );
};