import React, {useState} from "react";
import _ from "lodash";
import {Label} from "semantic-ui-react";

/**
 * Return the given ID or generate a uniq ID
 * @param id
 */
export const useID = (id: string | null | undefined) => {
  const [myID] = useState(id || _.uniqueId('myID-'));
  return myID;
};
/**
 * Stop enter submitting the form when used as onKeyDown even for form
 */
export const _onKeyDown = (keyEvent: KeyboardEvent) => {
  if (keyEvent.key === "Enter") {
    keyEvent.preventDefault();
  }
};
export const MyErrorLabel = ({error}: { error: string | undefined }) => {
  if (error === undefined) {
    return null;
  }
  return (
    <Label
      prompt={true}
      pointing='above'
      role='alert'
      aria-atomic={true}
    >
      {error}
    </Label>
  );
};