const getAstSign = (birthDate: string) => {
    const birthDay = parseInt(birthDate.split('/')[0]);
    const birthMonth = parseInt(birthDate.split('/')[1]);
    let number = -1, sign = "שגיאה";

    switch (birthMonth) {
        case 1:
            if (birthDay <= 20) {
                number = 8;
                sign = "גדי";
            } else {
                number = 11;
                sign = "דלי";
            }
            break;
        case 2:
            if (birthDay <= 18) {
                number = 11;
                sign = "דלי";
            } else {
                number = 7;
                sign = "דגים";
            }
            break;
        case 3:
            if (birthDay <= 20) {
                number = 7;
                sign = "דגים";
            } else {
                number = 9;
                sign = "טלה";
            }
            break;
        case 4:
            if (birthDay <= 20) {
                number = 9;
                sign = "טלה";
            } else {
                number = 33;
                sign = "שור";
            }
            break;
        case 5:
            if (birthDay <= 20) {
                number = 33;
                sign = "שור";
            } else {
                number = 5;
                sign = "תאומים";
            }
            break;
        case 6:
            if (birthDay <= 21) {
                number = 5;
                sign = "תאומים";
            } else {
                number = 2;
                sign = "סרטן";
            }
            break;
        case 7:
            if (birthDay <= 22) {
                number = 2;
                sign = "סרטן";
            } else {
                number = 1;
                sign = "אריה";
            }
            break;
        case 8:
            if (birthDay <= 22) {
                number = 1;
                sign = "אריה";
            } else {
                number = 4;
                sign = "בתולה";
            }
            break;
        case 9:
            if (birthDay <= 22) {
                number = 4;
                sign = "בתולה";
            } else {
                number = 6;
                sign = "מאזניים";
            }
            break;
        case 10:
            if (birthDay <= 23) {
                number = 6;
                sign = "מאזניים";
            } else {
                number = 22;
                sign = "עקרב";
            }
            break;
        case 11:
            if (birthDay <= 21) {
                number = 22;
                sign = "עקרב";
            } else {
                number = 3;
                sign = "קשת";
            }
            break;
        case 12:
            if (birthDay <= 21) {
                number = 3;
                sign = "קשת";
            } else {
                number = 8;
                sign = "גדי";
            }
            break;
    }
    return {
        number,
        sign,
    };
};

const shouldReduce = (num: number) => {
    return true;
}

const calcNum = (num: number, reduce=false) => {
    while (num > 9) {
        num = num.toString().split("").map((d)=>parseInt(d)).reduce((x,y) => x + y, 0);
    }
    return num;
}

const calcAevi = (name: string) => {
    return name.split("").filter((c) => aevi.has(c)).map((c) => (abc[c] || 0)).reduce((x,y) => x + y, 0);
};

const calcItzurim = (name: string) => {
    return name.split("").filter((c) => !aevi.has(c)).map((c) => (abc[c] || 0)).reduce((x,y) => x + y, 0);
};

const totalAevi = (first: string, last: string) => {
    return calcNum(calcNum(calcAevi(first)) + calcNum(calcAevi(last)));
};

const totalItzurim = (first: string, last: string) => {
    return calcNum(calcNum(calcItzurim(first)) + calcNum(calcItzurim(last)));
};

const totalName = (name: string) => {
    return calcNum(calcAevi(name) + calcItzurim(name));
};

const calcDay = (date: string) => {
    const splitted = date.split('/');
    let day = parseInt(splitted[0]);
    day = calcNum(splitted[0].split("").map((d)=>parseInt(d)).reduce((x,y) => x + y, 0));
    return day;
};

const calcDate = (date: string) => {
    const splitted = date.split('/');
    let day = parseInt(splitted[0]), month = parseInt(splitted[1]), year = parseInt(splitted[2]);
    if (shouldReduce(day)) {
        day = calcNum(splitted[0].split("").map((d)=>parseInt(d)).reduce((x,y) => x + y, 0));
    }
    if (shouldReduce(month)) {
        month = calcNum(splitted[1].split("").map((d)=>parseInt(d)).reduce((x,y) => x + y, 0));
    }
    if (shouldReduce(year)) {
        year = calcNum(splitted[2].split("").map((d)=>parseInt(d)).reduce((x,y) => x + y, 0));
    }
    return calcNum(day + month + year);
};

const calcMonth = (date: string) => {
    const splitted = date.split('/');
    let month = parseInt(splitted[1]);
    if (shouldReduce(month)) {
        month = calcNum(splitted[1].split("").map((d)=>parseInt(d)).reduce((x,y) => x + y, 0));
    }
    return month;
};

const showKarmaticNumber = (num: number) => {
    let numStr = num.toString();
    if (num >= 10) {
      if (shouldReduce(num) || num === 33) {
        numStr = calcNum(num, true) + '=' + num
      } else {
        numStr += '/' + calcNum(num, true);
      }
    }
    return numStr;
};

const showUniversalDay = (num: number) => {
    let numStr = num.toString();
    if (num === 29) {
        return '29=11/2'
    }
    if (num >= 10) {
      let delim = '/';
      if (shouldReduce(num)) {
        delim = '=';
      }
      numStr += delim + calcNum(num, true);
    }
    return numStr;
  };

const calcBirthday = (birthDate: string, current: string) => {
    const currentMonth = parseInt(current.split('/')[1]);
    const currentYear = parseInt(current.split('/')[2]);
    const birthMonth = parseInt(birthDate.split('/')[1]);
    const birthYear = parseInt(birthDate.split('/')[2]);
    let years = 0, months = 0, lastBirthday = birthDate.split('/')[0] + "/" + birthMonth + "/" + currentYear;
    if (currentMonth < birthMonth) {
        years -= 1;
        months = 12;
        lastBirthday = birthDate.split('/')[0] + "/" + birthMonth + "/" + (currentYear - 1);
    }
    years += (currentYear - birthYear);
    months -= (birthMonth - currentMonth);
    return {
        months,
        years,
        lastBirthday,
    };
};

const calcPeak = (date: string, peak: number) => {
    const splitted = date.split('/');
    let day = parseInt(splitted[0]), month = parseInt(splitted[1]), year = parseInt(splitted[2]);
    if (shouldReduce(day)) {
        day = calcNum(splitted[0].split("").map((d)=>parseInt(d)).reduce((x,y) => x + y, 0));
    }
    if (shouldReduce(month)) {
        month = calcNum(splitted[1].split("").map((d)=>parseInt(d)).reduce((x,y) => x + y, 0));
    }
    if (shouldReduce(year)) {
        year = calcNum(splitted[2].split("").map((d)=>parseInt(d)).reduce((x,y) => x + y, 0));
    }
    if (peak === 1) {
        return calcNum(day + month);
    }
    if (peak === 2) {
        return calcNum(day + year);
    }
    if (peak === 3) {
        return calcNum(calcNum(day + month) + calcNum(day + year));
    }
    if (peak === 4) {
        return calcNum(month + year);
    }
    return -1;
};

const calcHiddenPeak = (date: string, peak: number) => {
    const splitted = date.split('/');
    const sunChakra = calcNum(calcDate(date), true);
    let day = parseInt(splitted[0]), month = parseInt(splitted[1]), year = parseInt(splitted[2]);
    if (shouldReduce(day)) {
        day = calcNum(splitted[0].split("").map((d)=>parseInt(d)).reduce((x,y) => x + y, 0));
    }
    if (shouldReduce(month)) {
        month = calcNum(splitted[1].split("").map((d)=>parseInt(d)).reduce((x,y) => x + y, 0));
    }
    if (shouldReduce(year)) {
        year = calcNum(splitted[2].split("").map((d)=>parseInt(d)).reduce((x,y) => x + y, 0));
    }
    if (peak === 1) {
        return calcNum(day + month) + calcNum(day);
    }
    if (peak === 2) {
        return calcNum(day + year) + calcNum(month);
    }
    if (peak === 3) {
        return calcNum(calcNum(day + month) + calcNum(day + year)) + calcNum(year);
    }
    if (peak === 4) {
        return calcNum(month + year) + sunChakra;
    }
    return -1;
};

