const calcPersonalYear = (birthDate: string, cosmoDate: string) => {
    const age = calcBirthday(birthDate, cosmoDate);
    const sunChakraVal = calcDate(birthDate);
    var personalYearValue = calcNum(age.years + sunChakraVal);
    if (parseInt(birthDate.split('/')[1]) > 6) {
        personalYearValue = calcNum(personalYearValue + 1);
    }
    const hidden = calcHiddenPersonalYear(birthDate, cosmoDate);
    return {
        name: "שנה אישית + נסתרת",
        value: '(' + hidden.value + ') ' + personalYearValue.toString(),
        textColor: "black",
        bgColor: "white",
    };

};

