export const suggestNames = (firstName: string, lastName: string, desiredCreationChakra: number|null, desiredThroatChakra: number|null, gender: string) => {
    let names = girls;
    if (gender === "male") {
        names = boys;
    }
    let result: Array<string> = [];
    if (desiredCreationChakra == null && desiredThroatChakra == null) return result;
    for (let i = 0; i < names.length; i++) {
        let currentCreation = calcNum(totalName(firstName + names[i]) + totalName(lastName));
        let currentThroat = totalAevi(firstName + names[i], lastName);
        let isDesiredCreation = false;
        let isDesiredThroat = false;
        if (desiredCreationChakra == null || currentCreation === desiredCreationChakra) isDesiredCreation = true;
        if (desiredThroatChakra == null || currentThroat === desiredThroatChakra) isDesiredThroat = true;
        if (isDesiredCreation && isDesiredThroat) result.push(names[i]);
    }
    if (result.length === 0) result.push("לא נמצאו שמות");
    return result;
};