import React, { useEffect, useState } from 'react';
import './App.css';
import { Button, Container, Grid, GridColumn, Popup, Segment, Table, Dropdown, Divider, Form, Header } from 'semantic-ui-react';
import { Formik, FormikErrors, useFormikContext } from 'formik';
import { MyForm } from './components/Form/MyForm';
import { MyInput } from './components/Form/MyInput';
import { calcChakras, calcHeb, calcLifeMap, calcMagicSquare, calcOthers, calcOthers2, calcOthers3, currentDate, suggestNames, hebMonthToNum, calcCodeNumOthers } from './logic/Numerology';
import { ChakraRowProps, ChakraTableProps, MainFormProps, OtherFormValues, PersonInfo, HistoryDropDownProps, LifeMapRowProps, LifeMapCellProps, LifeMapTableProps, MagicSquareProps, HebrewDate } from './models/Interfaces';
import { MySelect } from './components/Form/MySelect';
import axios from 'axios';
import { sha256 } from 'js-sha256';
import { MyRadios } from './components/Form/MyRadios';

const validateMainForm = (values: OtherFormValues & PersonInfo) => {
  let errors: FormikErrors<OtherFormValues & PersonInfo> = {};
  if (!values.birthDate) {
    errors.birthDate = "שדה חובה";
  } else if(!/^\d{1,2}\/\d{1,2}\/\d{4}$/.test(values.birthDate.trim())) {
    errors.birthDate = "תאריך לא חוקי";
  }
  if(!/^\d{1,2}\/\d{1,2}\/\d{4}$/.test(values.cosmoDate.trim())) {
    errors.cosmoDate = "תאריך לא חוקי";
  }

  return errors;
};

type HistoryState = {[key: string]: PersonInfo};

const addToHistory = (elem: PersonInfo, histState: HistoryState, setHistState: React.Dispatch<React.SetStateAction<HistoryState>>) => {
  histState[elem.firstName+elem.lastName+elem.birthDate] = elem;
  setHistState(histState);
};

const getHistory = () => {
  const historyStr = localStorage.getItem("MainFormHistory");
  let history: HistoryState = {};
  if (historyStr !== null) {
    history = JSON.parse(historyStr);
  }
  return history;
};

const MainForm = (props: MainFormProps & {histState: HistoryState, setHistState: React.Dispatch<React.SetStateAction<HistoryState>>, formValues: OtherFormValues & PersonInfo, setFormValues: React.Dispatch<React.SetStateAction<OtherFormValues & PersonInfo>>}) => {
  const setFormValues = props.setFormValues;
  const histState = props.histState;
  useEffect(() => {
    const setHistory = () => {
      localStorage.setItem("MainFormHistory", JSON.stringify(histState));
    };
    return setHistory;
  });
  return (
    <Formik
      initialValues={props.formValues}
      validate={validateMainForm}
      onSubmit={(values, actions) => {
        console.log(values, actions);
        props.setFormValues(values);
        addToHistory(values, props.histState, props.setHistState);
        actions.setSubmitting(false);
      }}
    >
      {(props) => {
        return (
          <MyForm size="small" >
            <HistoryDropDown setFormValues={setFormValues} histState={histState}/>
            <Divider/>
            <MyInput style={{backgroundColor: "#ffffff"}} label="שם פרטי" name="firstName"></MyInput>
            <MyInput style={{backgroundColor: "#ffffff"}} label="שם משפחה" name="lastName"></MyInput>
            <MyInput style={{backgroundColor: "#ffffff"}} label="תאריך לידה" name="birthDate"></MyInput>
            <MySelect name="gender" style={{height: 35, backgroundColor: "#ffffff", textAlign: "right"}}>
              <option value="male">זכר</option>
              <option value="female">נקבה</option>
            </MySelect>
            <Form.Group>
              <MyInput style={{backgroundColor: "#ffffff"}} label="שם אמא" name="mother"></MyInput>
              <MyInput style={{backgroundColor: "#ffffff"}} label="שם אבא" name="father"></MyInput>
            </Form.Group>
            <MyRadios onChange={()=> {props.submitForm()}} name="psagotDate" label={"פסגות ואתגרים"} id={1} options={[{label: "לועזי", value: "loazi"}, {label: "עברי", value: "ivri"}]}></MyRadios>
            <Button type="submit" disabled={props.isSubmitting} size="large" style={{backgroundColor: "#ffffff22"}}>
                  ייצר
            </Button>
            <Button type="reset" onClick={()=>document.location.reload()}size="large" style={{backgroundColor: "#ffffff22"}}>
                  מחק
            </Button>
          </MyForm>
        );
      }}
    </Formik>
  );
};

const ChakraRow = (props: ChakraRowProps) => {
  let fw:"normal" | "bold" | "-moz-initial" | "inherit" | "initial" | "revert" | "unset" | (number & {}) | "bolder" | "lighter" | undefined = 'normal';
  if (props.boldName){
    fw = 'bold';
  }
  let vw:"normal" | "bold" | "-moz-initial" | "inherit" | "initial" | "revert" | "unset" | (number & {}) | "bolder" | "lighter" | undefined = 'normal';
  if (props.boldValue){
    vw = 'bold';
  }
  return (
    <Table.Row>
      <Table.Cell><div style={{fontWeight: fw}}>{props.name}</div></Table.Cell>
      <Table.Cell>
      <div style={{fontWeight: vw, fontSize: props.textSize, color: props.textColor, backgroundColor: props.bgColor, textAlign: "center", border: "1px solid black", width: 100, height: 30}}><span dir="ltr" style={{verticalAlign: "middle"}}>{props.value}</span></div>
      </Table.Cell>
    </Table.Row>
  );
};

const ChakraTable = (props: ChakraTableProps&{padded: boolean, size?: 'small' | 'large'}) => {
  return (
    <Table size={props.size || "large"} padded={props.padded} collapsing celled textAlign="right" style={{backgroundColor: "#ffffff22"}}>
      <Table.Body>
        {props.chakras && props.chakras.map((chakra: ChakraRowProps) => <ChakraRow key={chakra.name} {...chakra} textSize={props.textSize}/>)}
      </Table.Body>
    </Table>
  );
};

const NamesTable = (props: {names?: Array<string>}) => {
  return (
    <Grid columns={3}>
      {props.names && props.names.map(name => <Grid.Column style={{color: "black"}}>{name}</Grid.Column>)}
    </Grid>
  );
};

const LifeMapCell = (props: LifeMapCellProps) => {
  return (
    <Table.Cell>
      <div style={{fontSize:"150%", color: props.textColor, backgroundColor: props.bgColor, textAlign: "center", border: "1px solid black", width: 85, height: 26}}><span style={{verticalAlign: "middle"}}>{props.value}</span></div>
    </Table.Cell>
  );
};

const LifeMapRow = (props: LifeMapRowProps) => {
  return (
    <Table.Row>
      <LifeMapCell {...props.age}/>
      <LifeMapCell {...props.hiddenPeak}/>
      <LifeMapCell {...props.challenge}/>
      <LifeMapCell {...props.peak}/>
    </Table.Row>
  );
};

const LifeMapTable = (props: LifeMapTableProps) => {
  return (
    <Table size="small" textAlign="center" collapsing style={{backgroundColor: "#ffffff22"}}>
      <Table.Header>
        <Table.Row>
          <Table.HeaderCell style={{backgroundColor: "#ffffff22"}}>גיל</Table.HeaderCell>
          <Table.HeaderCell style={{backgroundColor: "#ffffff22"}}>פסגה נסתרת</Table.HeaderCell>
          <Table.HeaderCell style={{backgroundColor: "#ffffff22"}}>אתגר</Table.HeaderCell>
          <Table.HeaderCell style={{backgroundColor: "#ffffff22"}}>פסגה</Table.HeaderCell>
        </Table.Row>
      </Table.Header>
      <Table.Body>
        {props.values && props.values.map((row: LifeMapRowProps, index: number) => <LifeMapRow key={index} {...row}/>)}
      </Table.Body>
    </Table>
  );
};

const HistoryElement = (props: PersonInfo & HistoryDropDownProps & {setValues: (values: unknown, shouldValidate?: boolean | undefined) => void}) => {
  const combined = props.firstName + " " + props.lastName + " " + props.birthDate;
  return {
    key: combined,
    value: combined,
    text: (
      <div
        style={{textAlign: "right"}}
      >
        {combined}
      </div>
    ),
    onMouseDown: (() => {
      props.setValues({
        firstName: props.firstName,
        lastName: props.lastName,
        birthDate: props.birthDate,
        cosmoDate: currentDate(),
        wantedThroat: "",
        wantedCreation: "",
        gender: props.gender,
        mother: props.mother,
        father: props.father,
      });
      props.setFormValues({
        firstName: props.firstName,
        lastName: props.lastName,
        birthDate: props.birthDate,
        cosmoDate: currentDate(),
        wantedThroat: "",
        wantedCreation: "",
        gender: props.gender,
        mother: props.mother,
        father: props.father,
        psagotDate: "",
      });
    }),
  };
};

const HistoryDropDown = (props: HistoryDropDownProps & {histState: HistoryState}) => {
  let history: PersonInfo[] = Object.values(props.histState);
  const { setValues } = useFormikContext();
  return (
    <Dropdown
      placeholder="היסטוריה"
      search={(options, value) => {return options.filter((elem) => {return elem.value?.toString().includes(value)})}}
      selection
      fluid
      style={{textAlign: "right", backgroundColor: "#ffffff"}}
      options={history.map((elem) => {
        return HistoryElement({
          firstName: elem.firstName,
          lastName: elem.lastName,
          birthDate: elem.birthDate,
          mother: elem.mother,
          father: elem.father,
          gender: elem.gender,
          setValues: setValues,
          setFormValues: props.setFormValues
        })
      })}
    />
  );
};

