function App() {
  const [hebDate, setHebDate] = useState<HebrewDate>({gy: '', gm: '', gd: '', afterSunset: '', hy: '', hm: '', hd: '', hebrew: '', events: []});
  const [hebDateString, setHebDateString] = useState('');
  const [histState, setHistState] = useState(getHistory());
  const [formValues, setFormValues] = useState({psagotDate: "", firstName: "", lastName: "", birthDate: "", cosmoDate: currentDate(), wantedThroat: "", wantedCreation: "", gender: "male", mother: "", father: ""});
  useEffect(() => {
    if (formValues.birthDate !== '') {
      const splitted = formValues.birthDate.split('/');
      axios.get(`https://www.hebcal.com/converter?cfg=json&gy=${splitted[2]}&gm=${splitted[1]}&gd=${splitted[0]}&g2h=1&lg=h`).then((response) => {
        setHebDate(response.data);
        setHebDateString(response.data['hd']+'/'+hebMonthToNum[response.data['hm']]+'/'+response.data['hy']);
        console.log(response.data['hd']+'/'+hebMonthToNum[response.data['hm']]+'/'+response.data['hy']);
        console.log(response.data);
    });
    }
  }, [formValues.birthDate]);
  const hash = sha256(document.location.search);
  const expected = [
    '1623c90550e6a136e98d3c4e54a3cd7c3695b9d8c0c157d93acb0567b33d15cc', // Imush
    //'fc1ee2fcd475b82c45faf8b5822ca97c1906afb568d6cdad146838462507b182', // Guest
  ]
  if (!expected.includes(hash)) {
    return (
      <div>אין גישה</div>
    )
  }
  return (
    <Container dir="rtl" style={{width: "100%"}} textAlign={"center"}>
      <Segment size="small" padded inverted tertiary color="violet">
        <Grid columns={3} stackable textAlign={"center"}>
          <Grid.Column width={3}>
            <MainForm formValues={formValues} setFormValues={setFormValues} histState={histState} setHistState={setHistState}/>
          </Grid.Column>
          <Divider hidden/>
          <GridColumn width={6}>
            <LifeMapTable values={calcLifeMap(formValues.psagotDate === 'ivri' ? hebDateString : formValues.birthDate)}/>
            <Divider hidden/>
            <Grid columns={2}>
              <GridColumn>
                <ChakraTable size="small" padded={false} chakras={calcOthers(formValues.lastName, formValues.birthDate, formValues.cosmoDate)}/>
              </GridColumn>
              <GridColumn>
                <ChakraTable size="small" padded={false} chakras={calcOthers2(formValues.firstName, formValues.gender, formValues.mother, formValues.father).concat(calcCodeNumOthers(formValues.birthDate, formValues.cosmoDate)).concat(calcHeb(hebDate))}/>
              </GridColumn>
            </Grid>
          </GridColumn>
          <Divider hidden/>
          <Grid.Column width={4}>
            <ChakraTable padded={true} textSize={"150%"} chakras={calcChakras(formValues.firstName, formValues.lastName, formValues.birthDate, formValues.cosmoDate)}/>
          </Grid.Column>
        </Grid>
      </Segment>
    </Container>
  );
}

export default App;
