import {FieldAttributes, useFormikContext} from "formik";
import React from "react";
import {Form} from "semantic-ui-react";

import {_onKeyDown} from "./tools";

export const MyForm = ({children, ...props}: FieldAttributes<any> & { children: React.ReactNode }) => {
  const {handleReset, handleSubmit} = useFormikContext();
  return (
    <Form onSubmit={handleSubmit} onReset={handleReset} onKeyDown={_onKeyDown} {...props}>
      {children}
    </Form>
  );
};

