import {FieldAttributes, useField, useFormikContext} from "formik";
import {MyErrorLabel, useID} from "./tools";
import React from "react";
import {Form, FormField} from "semantic-ui-react";

export const MyRadioInput = (props: FieldAttributes<any>) => {
  const [field, meta] = useField({...props, type: 'radio'});
  return (
    <>
      <input type="radio" {...props} {...field}
             checked={meta.value === props.value}/>
    </>
  )
};
export const MyRadios = ({options, label, id, ...props}: FieldAttributes<any>) => {
  const {isSubmitting} = useFormikContext();
  const myID = useID(id);
  const [ ,meta] = useField(props);

  const radios = options.map((opt: { value: string, label: string | React.ReactNode }) => {
    return (
      <FormField {...props} key={opt.value}>
        <div className="ui radio checkbox">
          <MyRadioInput {...props} value={opt.value} id={myID + "-" + opt.value} disabled={isSubmitting}/>
          <label htmlFor={myID + "-" + opt.value}>{opt.label}</label>
        </div>
      </FormField>
    );
  });

  return (
    <Form.Field error={meta.touched && !!meta.error}>
      <label>{label}</label>
      {radios}
      <MyErrorLabel error={meta.touched && meta.error ? meta.error : undefined}/>
    </Form.Field>
  )
};